@font-face {
  font-weight: bold;
  font-family: "Montserrat";
  font-style: normal;
  font-display: swap;
  src: url("./../fonts/Montserrat-Bold.woff2") format("woff2"), url("./../fonts/Montserrat-Bold.woff") format("woff"); }

html {
  height: 100%;
  box-sizing: border-box; }

* {
  box-sizing: inherit; }
  *::before, *::after {
    box-sizing: inherit; }

body {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 320px;
  width: 100%;
  min-height: 100%;
  font-family: "FiraSans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 170%;
  color: #484848; }

img {
  max-width: 100%;
  height: auto; }

picture {
  font-size: 0;
  line-height: 0; }

